<template lang="pug">
section.blogs
    .container
        .title
            h2
                span blogs
        .blogs-content
            .row
                div(class="col-md-6 col-lg-4 box" v-for="blog in BlogsData.blogs", :key="blog.id")
                    .blogs-box.wow.fadeInUp(data-wow-duration="1s")
                        .blogs-image
                            img(:src="blog.image", alt="")
                        .blogs-text
                            h3 {{ blog.title }}
                            p {{ blog.body }}
                        .blogs-buttons
                            a.btn.hvr-sweep-to-bottom(href="single-blog.html") See more
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "TheBlogs",
    mounted() {
        this.$store.dispatch("getBlogsData");
    },
    computed: {
        ...mapState(["BlogsData"]),
    },
};
</script>
